/* Paste the styles from SliderComponent.css here */

.slider-container {
  width: 100%;
  overflow: hidden;
}

.slider {
  display: flex;
  transition: transform 0.5s ease-in-out;
  position: relative; /* Add position relative to the slider */
}

.slide {
  display: flex; /* Make slides flex containers */
  flex-direction: column; /* Arrange content vertically */
  min-width: 100%;
  height: 800px;
  background-size: cover;
  background-position: center;
  position: relative; /* Add position relative to the slide */
}
.button1 {
  font-size: 18px; /* Adjust the font size as needed */
  background-color: rgba(0, 174, 255, 0); /* Transparent background */
  color: rgb(0, 174, 255); /* Text color */
  border: 1px solid rgb(0, 174, 255); /* Border color */
  border-radius: 30px; /* Border radius */
  padding: 10px 30px; /* Padding */
  cursor: pointer; /* Ensure the cursor changes to a pointer on hover */
  transition: all 0.4s ease-in-out; /* Smooth transition */
  /* Positioning */
  position: absolute;
  bottom: 400px; /* Adjust the distance from the top */
  left: 50%;
  transform: translateX(-50%);
}
.button1:hover {
  background-color: rgba(0, 174, 255, 0.1); /* Add a slight background color on hover */
  color: #fff; /* Change text color on hover */
}




/* Adjust animation duration for the first slide to stay longer */
.slide:nth-child(1) {
  animation: slideAnimation 15s forwards; /* Change the duration for the first slide */
}

/* Adjust animation duration for slides after the first one */
.slide:nth-child(n+2) {
  animation: slideAnimation 15s forwards; /* Change the duration for slides after the first one */
}

.slide-image {
  width: 100%; /* Ensure images fill the width of their container */
}

.slide-content {
  position: absolute; /* Position absolute to overlay on the image */
  top: 50%; /* Position the content in the middle vertically */
  left: 50%; /* Position the content in the middle horizontally */
  transform: translate(-50%, -50%); /* Center the content */
  padding: 10px;
  color: rgb(255, 255, 255);
  
  text-align: center;
  background-color: rgba(255, 255, 255, 0); /* Semi-transparent background */
  border-radius: 10px; /* Rounded corners */
  animation: fadeInAnimation 2s forwards; /* Add animation to slide content */
}


.slide-content, .left, .right {
  opacity: 0;
  animation: fadeInAnimation 2s forwards;
}
@keyframes fadeInAnimation {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
