  .marquee span {
    display: inline-block;
    padding-left: 100%;
    animation: marquee 20s linear infinite;
    font-size: 57px;
    font-weight: bold;
  text-transform: uppercase;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #004D46;
  color: transparent;
  background-image: linear-gradient(90deg, #00214D, #00ADEE);
  background-size: 200% auto;
  background-clip: text;
  -webkit-background-clip: text;
  
  
  }
  @keyframes flowing-water {
    0% {
      background-position: 100% 50%;
    }
    50% {
      background-position: 0% 50%;
    }
    100% {
      background-position: 100% 50%;
    }
  }
  
  .marquee span:hover {
    animation-play-state: paused;
    color: #fff; /* Change to your desired hover color */
  }  
  
  @keyframes marquee {
    0%   { transform: translateX(100%); }
    0%   { transform: translateX(100%); } /* Start delay */
    1%   { transform: translateX(0); }   /* Show text for 2 seconds */
    100% { transform: translateX(-100%); }
  }
  