.form-container {
    display: flex;
    align-items: center;
    
    justify-content: center;
    height: 100vh;
    background-image: url('https://raw.githubusercontent.com/AkshatJain1900/images/2be1b5758de5556d1ea20ee39e8e67fedf3a6f8d/Untitled%20(1232%20x%20696%20px)%20(696%20x%20330%20px)%20(933%20x%20750%20px)%20(1).svg');
    background-size: cover;
}
  
  .register-form {
    border: 1px solid gray;
    padding: 30px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2); /* Add a subtle shadow */
   
    margin-top: 180px;
  }
  .register-form h3 {
    color: #268A80; /* Change the color to your desired color */
  }
  /* Glowing effect animation */
  @keyframes glow {
    0% {
      box-shadow: 0 0 10px rgba(0, 174, 255, 0.8);
    }
    50% {
      box-shadow: 0 0 20px rgba(0, 174, 255, 0.8);
    }
    100% {
      box-shadow: 0 0 10px rgba(0, 174, 255, 0.8);
    }
  }
  
  .form-container:hover .register-form {
    animation: glow 1s infinite alternate; /* Apply the animation on hover */
  }
  